.carousel {
  position: relative;
  height: 100%;
  width: 100%;

  &__container {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    transition: transform .6s ease-in-out;
  }

  &__group {
    position: absolute;
    top:0;
    left:0;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }

  &__item {
    flex-grow: 1;
    flex-shrink: 0;
    img {
      min-width: 100%;
      min-height: $img-height-large;
      object-fit: cover;
    }
  }

  &__counter {
    position: absolute;
    display: flex;
    justify-content: center;
    bottom: 0;
    width: 100%;
    height: 15%;    

    p {
      font-size: 18px!important;
      color:white;
    }    
  }

  &__control{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:spacer();

    &__next, &__previous {
      background-image: url(../../assets/arrow-next.svg);
      background-position: center;
      background-repeat: no-repeat;
      height:80px;
      width: 50px;
      overflow: hidden;
      text-indent: 999px;
      cursor: pointer;
    }

    &__next {
      transform: rotate(180deg);
    }
  }
}

@media (max-width: $screen-sm) {
  .carousel {
    &__control {
      &__next, &__previous {
        height: 20px;
        width: 10px;
        background-image: url(../../assets/arrow-next-sm.svg);
      }
    }

    &__counter {
      display:none;
    }
  }
}